.error-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh; 
    text-align: center;
    background-color: #f8f9fa; 
  }
  
  .error-image {
    max-width: 100%; 
    height: auto;
    margin-bottom: 20px; 
  }
  
  .error-message {
    font-size: 24px; 
    color: #343a40; 
    margin: 0;
  }
  
  .error-description {
    font-size: 16px; 
    color: #6c757d; 
  }
  