/* EnrolledTableData.css */

/* Make the table container scrollable horizontally */
.table-container {
    max-width: 100%;
    overflow-x: auto;
    border-radius: 10px;
  }
  
/* Default styles for sticky columns (applies to all tables by default) */
.rdt_TableCell:last-child,
.rdt_TableCol:last-child {
  position: sticky;
  right: 0;
  background-color: #1f1f1f;
  z-index: 1;
  border-right: 1px solid #585858;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Override for the non-sticky page */
.nonStickyPage .rdt_TableCell:last-child,
.nonStickyPage .rdt_TableCol:last-child {
  position: relative; 
  right: auto;
  z-index: 0;
  display: flex;
  justify-content: start;
}